h1,
h2,
h3 {
  margin-bottom: 1.25rem;
}

p, blockquote {
  line-height: 1.6;
}

a,
a:visited,
a:active {
  text-decoration: none;
  background-color: transparent;
}

ul {
  list-style-type: none;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  * {
    transition-duration: 0.01ms !important;
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto !important;
  }
}
.sr-only {
  position: absolute !important;
  margin: -1px !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  border: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  overflow: hidden !important;
  white-space: nowrap !important;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

::selection {
  background-color: #72bc38;
  color: #f2f2f2;
}

::-moz-selection {
  background-color: #72bc38;
  color: #f2f2f2;
}

html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: "League Spartan", sans-serif;
}

body {
  background: top left no-repeat, bottom right no-repeat;
  background-image: url("../images/bg-pattern-top-mobile.svg"), url("../images/bg-pattern-bottom-mobile.svg");
  color: hsl(300deg, 43%, 22%);
  min-height: 100vh;
}
@media (min-width: 37.5em) {
  body {
    background-image: url("../images/bg-pattern-top-desktop.svg"), url("../images/bg-pattern-bottom-desktop.svg");
  }
}

main {
  padding: 4rem 2rem;
}
@media (min-width: 62.5em) {
  main {
    padding: 5rem 2rem;
  }
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

@media (min-width: 62.5em) {
  .landing-cards-widget {
    display: flex;
    gap: 1.25rem;
    grid-column: 1/3;
  }
}

.landing-cards {
  position: relative;
  background-color: hsl(300deg, 43%, 22%);
  padding: 1.25rem;
  border-radius: 0.625rem;
  color: #f2f2f2;
}
@media (min-width: 62.5em) {
  .landing-cards {
    padding: 2.5rem;
  }
}

.landing-cards:not(:first-child) {
  margin-top: 2.45rem;
}
@media (min-width: 62.5em) {
  .landing-cards:not(:first-child) {
    margin-top: 0;
  }
}

@media (min-width: 62.5em) {
  .landing-cards:nth-child(2) {
    top: 1.25rem;
  }
}

@media (min-width: 62.5em) {
  .landing-cards:last-child {
    top: 2.5rem;
  }
}

.landing-cards-figure {
  display: flex;
  align-items: center;
  margin-bottom: 0.9375rem;
}

.landing-card-img {
  width: 3.125rem;
  height: 3.125rem;
  border-radius: 50%;
  margin-right: 0.9375rem;
}

.landing-cards-figcaption {
  padding: 0.9375rem;
}

.landing-card-name {
  font-size: 1.25rem;
  font-weight: 700;
}

.landing-card-buyer {
  color: hsl(333deg, 80%, 67%);
  font-size: 1.25rem;
  font-weight: 500;
  line-height: 1.1;
}

.landing-card-story {
  font-size: 1rem;
  font-weight: 500;
  line-height: 1.4;
}

.landing-rated-wrapper {
  margin: 3.25rem 0;
}
@media (min-width: 62.5em) {
  .landing-rated-wrapper {
    margin: 0;
  }
}

.landing-rated-item {
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  padding: 1.0625rem;
  background-color: hsl(300deg, 24%, 96%);
  margin: 0.9375rem 0;
  border-radius: 0.5rem;
}
@media (min-width: 80em) {
  .landing-rated-item {
    flex-direction: row;
    padding: 1.4375rem;
    width: 28.125rem;
  }
}

@media (min-width: 80em) {
  .landing-rated-item:nth-child(2) {
    left: 3.75rem;
  }
}

@media (min-width: 80em) {
  .landing-rated-item:last-child {
    left: 7.5rem;
  }
}

.landing-rated-star {
  display: flex;
  gap: 0.3125rem;
  margin-bottom: 0.9375rem;
}
@media (min-width: 80em) {
  .landing-rated-star {
    margin-bottom: 0;
  }
}

.landing-rated-text {
  font-size: 1.0625rem;
  font-weight: 700;
  margin-left: 0;
}
@media (min-width: 80em) {
  .landing-rated-text {
    margin-left: 2.5rem;
  }
}

.landing {
  display: block;
  max-width: 31.25rem;
  margin: 0 auto;
}
@media (min-width: 62.5em) {
  .landing {
    display: grid;
    align-items: center;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 3.75rem;
    margin: 0 auto;
    max-width: 75rem;
  }
}

.landing-info {
  text-align: center;
}
@media (min-width: 62.5em) {
  .landing-info {
    max-width: 31.25rem;
    text-align: left;
  }
}

.landing-info-title {
  font-size: 2.5rem;
  line-height: 0.8;
}
@media (min-width: 62.5em) {
  .landing-info-title {
    font-size: 3.75rem;
    line-height: 0.8;
  }
}

.landing-info-description {
  font-size: 1.0625rem;
}
@media (min-width: 62.5em) {
  .landing-info-description {
    font-size: 1.25rem;
  }
}